export default function animation (start, end, dom, direction) {
  let dis = 0
  let speed = 5
  if (end < 0) {
    speed *= -1
  }
  const timer = setInterval(() => {
    dis += speed
    dom[direction] = start + dis
    if (Math.abs(dis) > Math.abs(end)) {
      dom[direction] = start + end
      clearInterval(timer)
    }
  }, 5)
}
