<template>
  <div class="search-container">
    <div class="header">
      <van-icon name="arrow-left" @click="$router.push({name: 'classify'})" />
      <van-search
          v-model="value"
          show-action
          placeholder="酒"
          @input="input"
          @focus="focus"
          @search="searchEvent"
          @blur="blur"
      >
          <template #action v-if="showsearch">
              <div>搜索</div>
          </template>
          <template #action v-else>
              <van-icon :badge="badge" name="shopping-cart-o" size="45" @click="$router.push({name: 'shopping'})" />
          </template>
      </van-search>
    </div>
    <div class="searchlist">
        <van-list>
            <van-cell v-for="item in searchList" :key="item" :title="item" @click="searchEvent(item)" />
        </van-list>
    </div>
    <div class="shoppingList" v-if="showshoppingList">
      <Commodity v-for="item in shoppinglist" :key="item.id" :Data="item" :num="shoppingRecords[item.id]"/>
      <div class="text">
        {{text}}
      </div>
    </div>
    <div class="history" v-if="showhisoryList">
      <h4>历史搜索</h4>
      <div class="historylist">
        <div v-for="item in historyList" :key="item" @click="searchEvent(item)">{{item}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/search.js'
import { mapState } from 'vuex'
import Commodity from '@/components/Commodity.vue'
export default {
  components: {
    Commodity
  },
  data () {
    return {
      value: '',
      showsearch: true,
      showshoppingList: false,
      showhisoryList: true,
      searchList: [],
      shoppinglist: [],
      historyList: [],
      timer: null,
      text: ''
    }
  },
  computed: {
    ...mapState({
      shoppingRecords: (state) => state.shoppingRecords
    }),
    badge () {
      const badge = Object.values(this.shoppingRecords).reduce((prev, next) => prev + next, 0)
      if (badge > 99) {
        return '99+'
      }
      if (badge === 0) {
        return null
      }
      return badge
    }
  },
  methods: {
    input () {
      this.showshoppingList = false
      if (this.value === '') {
        this.searchList = []
        this.showsearch = true
        this.showhisoryList = true
        return
      } else {
        this.showsearch = false
        this.showhisoryList = false
      }
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      } else {
        this.timer = setTimeout(async () => {
          if (this.value === '') {
            return
          }
          const value = await api.fuzzySearch({ likeValue: this.value })
          this.searchList = value.data.result
          clearTimeout(this.timer)
          this.timer = null
        }, 300)
      }
    },
    focus () {
      if (this.value !== '') {
        this.showsearch = false
        this.showhisoryList = false
      }
    },
    blur () {
      if (this.value === '') {
        this.showsearch = true
        this.showhisoryList = true
      }
    },
    searchEvent (item) {
      if (!this.historyList.find(value => value === item)) {
        if (this.historyList.length > 10) {
          this.historyList.pop()
        }
        this.historyList.unshift(item)
      }
      localStorage.setItem('history', JSON.stringify(this.historyList))
      this.value = item
      this.showsearch = true
      this.showhisoryList = false
      this.searchList = []
      this.shoppingList()
      console.log('search')
    },
    async shoppingList () {
      const shoppingList = await api.search({ type: this.value, page: 1, size: 10 })
      this.shoppinglist = shoppingList.data.list
      this.showsearch = false
      this.showshoppingList = true
      if (shoppingList.data.total > 10) {
        this.text = '加载更多'
      } else {
        this.text = '没有了'
      }
    }
  },
  created () {
    this.historyList = JSON.parse(localStorage.getItem('history')) || []
  }
}
</script>

<style lang="less" scoped>
.search-container{
    width: 100%;
    height: 100vh;
    position: absolute;
    animation: search .3s linear;
    @keyframes search {
      0% {
        left: 100%;
      }
      100% {
        left: 0;
      }
    }
    .header{
        display: flex;
        width: 100%;
        align-items: center;
        padding: 0 10px;
        box-sizing: border-box;
        .van-icon{
          font-size: 30px;
          color: #666;
        }
        .van-search{
            flex: 1;
            .van-search__action{
                height: 100%;
                line-height: 100%;
            }
        }
    }
    .searchlist{
        padding: 0 10%;
        .van-cell{
            border-bottom: 1px solid#eee;
        }
    }
    .shoppingList{
      padding: 0 30px;
      box-sizing: border-box;
      .text {
        width: 100%;
        height: 50px;
        font-size: 24px;
        text-align: center;
        line-height: 50px;
        color: #ddd;
      }
    }
    .history{
      width: 100%;
      padding: 0 30px;
      box-sizing: border-box;
      .historylist{
        display: flex;
        flex-wrap: wrap;
        div{
          margin: 5px 10px;
          padding: 3px 5px;
          background: #eee;
          border-radius: 3px;
        }
      }
    }
}
</style>
