<template>
  <div class="home-container" ref="home">
    <div class="header">
      <van-icon name="fire" color="#ee0a24" />
      <van-icon name="chat-o" dot />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
        :class="{shang: showshang, xia: showxia}"
      />
    </div>
    <div class="bodyer" ref="bodyer">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item>你</van-swipe-item>
        <van-swipe-item>好</van-swipe-item>
        <van-swipe-item>啊</van-swipe-item>
        <van-swipe-item>！</van-swipe-item>
      </van-swipe>
      <div class="cardgroup">
        <div class="card" v-for="item in 7" :key="item">
          <div></div>
          <span>我是第 {{item}} 个</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: '',
      showshang: false,
      showxia: false
    }
  },
  methods: {
    scroll () {
      if (this.$refs.bodyer.getBoundingClientRect().top < 148) {
        this.showshang = true
        this.showxia = false
      }
      if (this.$refs.bodyer.getBoundingClientRect().top > 148) {
        this.showshang = false
        this.showxia = true
      }
    }
  },
  mounted () {
    function throttle (fn, delay) {
      let time = null
      return function () {
        if (!time) {
          time = setTimeout(() => {
            time = null
            fn.call(this)
          }, delay)
        }
      }
    }
    this.$refs.home.addEventListener('scroll', throttle(this.scroll, 300))
  }
}
</script>

<style lang="less" scoped>
.home-container{
  width: 100%;
  height: 100vh;
  overflow: auto;
  transition: all 1s;
  .header{
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background: #fff;
    .van-icon{
      font-size: 28px;
    }
    .van-icon-fire{
      float: left;
      margin: 5px;
    }
    .van-icon-chat-o{
      float: right;
      margin: 5px 7px;
    }
    .van-search{
      height: 38px;
      width: 100%;
      margin-left: 0;
    }
    .shang{
      animation: shang 1s;
      animation-fill-mode: forwards;
    }
    .xia{
      animation: xia 1s;
      animation-fill-mode: forwards;
    }
    @keyframes shang{
      0%{
        height: 38px;
        width: 100%;
        margin-left: 0;
      }
      50%{
        width: calc(100% - 80px);
        transition: all 1s;
        margin-left: 35px;
        margin-top: 38px;
      }
      100%{
        width: calc(100% - 80px);
        transition: all 1s;
        margin-left: 35px;
        margin-top: 0px;
      }
    }
    @keyframes xia{
      0%{
        width: calc(100% - 80px);
        transition: all 1s;
        margin-left: 35px;
        margin-top: 0px;
      }
      50%{
        width: calc(100% - 80px);
        transition: all 1s;
        margin-left: 35px;
        margin-top: 38px;
      }
      100%{
        height: 38px;
        width: 100%;
        margin-left: 0;
      }
    }
  }
  .header::after{
    content: "";
    display: block;
    clear: both;
  }
  .bodyer{
    margin-top: 76px;
    margin-bottom: 50px;
    .my-swipe {
      color: #fff;
      font-size: 40px;
      line-height: 150px;
      text-align: center;
      background-color: #39a9ed;
    }
    .cardgroup{
      width: 100%;
      .card{
        width: 50%;
        height: 200px;
        display: inline-block;
        text-align: center;
        div{
          margin: 10px;
          height: 120px;
          background: #0f0;
          border-radius: 10px;
        }
      }
    }
  }
}
.home-container::-webkit-scrollbar{
    width: 0px;
    background: none;
  }
</style>
