import axios from 'axios'

const instance = axios.create({
  baseURL: 'https://mallapi.duyiedu.com/goods/'
})

// 请求拦截
instance.interceptors.request.use((request) => {
  return {
    ...request,
    params: {
      ...request.params,
      appkey: 'dd_1597654682810'
    }
  }
})

export default instance
