<template>
  <div class="onetab-container" ref="onetab">
    <div
      class="tab-item"
      v-for="(item, i) in menuList"
      :key="item.title"
      :class="{active: index == i}"
      @touchend="scrollTo(i, $event)"
      @touchstart="move = false"
      @touchmove="move = true"
    >
      <div class="img">
        <img :src="item.imgURL" alt="">
      </div>
      <div class="title">{{item.title}}</div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import animation from '@/until/animation.js'
export default {
  data () {
    return {
      index: 0,
      move: false,
      menuList: [
        {
          title: '时令水果',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/时令水果.jpg'
        },
        {
          title: '酒水冲调',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/酒水冲调.jpg'
        },
        {
          title: '安心乳品',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/安心乳品.jpg'
        },
        {
          title: '休闲零食',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/休闲零食.jpg'
        },
        {
          title: '肉蛋食材',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/肉蛋食材.jpg'
        },
        {
          title: '新鲜蔬菜',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/新鲜食材.jpg'
        },
        {
          title: '熟食餐饮',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/熟食餐饮.jpg'
        },
        {
          title: '海鲜水产',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/海鲜水产.jpg'
        },
        {
          title: '粮油调味',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/粮油调味.jpg'
        },
        {
          title: '某手美食',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/某手美食.jpg'
        },
        {
          title: '纸杯清洁',
          imgURL:
            'https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/纸品清洁.jpg'
        }
      ]
    }
  },
  methods: {
    ...mapActions(['getTwotab']),
    scrollTo (i, e) {
      if (this.move) {
        return
      }
      this.index = i
      const itemWidth = e.target.offsetWidth // 元素的宽度
      const itemLeft = e.target.getBoundingClientRect().left // 点击的位置距离左边的距离
      const onetabWidth = this.$refs.onetab.offsetWidth
      animation(this.$refs.onetab.scrollLeft, itemWidth / 2 + itemLeft - onetabWidth / 2, this.$refs.onetab, 'scrollLeft')
      this.getTwotab({ type: this.menuList[i].title })
    }
  },
  mounted () {
    this.getTwotab({ type: this.menuList[0].title })
  }
}
</script>

<style lang="less" scoped>
.onetab-container{
  width: 375px;
  height: 104px;
  display: flex;
  overflow: auto;
  .tab-item{
    width: 60px;
    height: 70px;
    padding: 10px 5px;
    .img{
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      border: 2px solid #fff;
      overflow: hidden;
      img{
        width: 50px;
        height: 50px;
      }
    }
    .title{
      text-align: center;
      font-size: 12px;
      margin-top: 5px;
    }
  }
  .active{
    .img{
      border: 2px solid #d13193;
    }
    .title{
      color: #fff;
      font-weight: bold;
      background: #d13193;
      border-radius: 30px;
    }
  }
}
.onetab-container::-webkit-scrollbar{
  width: 0px;
  background: none;
}
</style>
