<template>
  <div class="classify-container">
    <Router-link class="search-btn" :to="{name: 'search'}" tag="div">
      <van-icon name="search" />
      商品打一折了
    </Router-link>
    <OneTab />
    <template v-if="showContent">
      <TwoTab />
      <ClassifyContent />
    </template>
    <van-loading type="spinner" vertical v-else />
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import OneTab from '@/components/OneTab.vue'
import TwoTab from '@/components/TwoTab.vue'
import ClassifyContent from '@/components/ClassifyContent.vue'
export default {
  components: {
    OneTab,
    TwoTab,
    ClassifyContent
  },
  computed: {
    ...mapState({
      showContent: (state) => state.showContent,
      twobarList: (state) => state.twobarList
    })
  },
  methods: {
    ...mapMutations(['resetClassifyContent']),
    ...mapActions(['getClassifyContent'])
  },
  watch: {
    showContent () {
      if (this.showContent) {
        this.resetClassifyContent()
        this.getClassifyContent({ type: this.twobarList[0], page: 1, sortType: 'all' })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.classify-container {
  width: 374px;
  .search-btn{
    width: 355px;
    height: 33px;
    line-height: 33px;
    background-color: #eee;
    color: #a1a1a1;
    margin: 11px auto 0;
    border-radius: 10px;
    font-size: 14px;
    text-align: center;
    >* {
      display: inline-block;
      vertical-align: middle;
    }
  }
}
</style>
