<template>
  <div class="app-container">
    <router-view></router-view>
    <Tabbar v-if="!(this.$router.history.current.path === '/search')" />
  </div>
</template>

<script>
import Tabbar from '@/components/Tabbar.vue'
export default {
  components: {
    Tabbar
  },
  created () {
    const shoppingRecords = JSON.parse(localStorage.getItem('goods')) || {}
    this.$store.commit('setshoppingRecords', shoppingRecords)
  }
}
</script>

