<template>
  <van-tabbar v-model="active" route fixed>
    <van-tabbar-item icon="home-o" replace to="/home">首页</van-tabbar-item>
    <van-tabbar-item icon="apps-o" replace to="/classify">分类</van-tabbar-item>
    <van-tabbar-item icon="shopping-cart-o" :badge="badge" replace to="/shopping">购物车</van-tabbar-item>
    <van-tabbar-item icon="friends-o" replace to="/my">我的</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      active: 'home'
    }
  },
  computed: {
    ...mapState({
      shoppingRecords: (state) => state.shoppingRecords
    }),
    badge () {
      const badge = Object.values(this.shoppingRecords).reduce((prev, next) => prev + next, 0)
      if (badge > 99) {
        return '99+'
      }
      if (badge === 0) {
        return null
      }
      return badge
    }
  }
}
</script>

