import Vue from 'vue'
import Vuex from 'vuex'
import api from '@/api/classify.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    type: null,
    showContent: false,
    twobarList: [],
    classifyContent: [],
    size: 8,
    shoppingRecords: {}
  },
  mutations: {
    getTwotab (state, payload) {
      state.type = payload
    },
    showContent (state, payload) {
      state.showContent = payload
    },
    twobarList (state, payload) {
      state.twobarList = payload
    },
    resetClassifyContent (state) {
      state.classifyContent = []
    },
    setClassifyContent (state, payload) {
      state.classifyContent = [...state.classifyContent, ...payload]
    },
    setshoppingRecords (state, payload) {
      state.shoppingRecords = payload
    },
    localRecords (state, payload) {
      if (state.shoppingRecords[payload.id]) {
        if ((state.shoppingRecords[payload.id] === 1 && payload.value === -1) || payload.value === -2) {
          Vue.delete(state.shoppingRecords, payload.id)
        } else {
          Vue.set(state.shoppingRecords, payload.id, state.shoppingRecords[payload.id] + payload.value)
        }
      } else {
        Vue.set(state.shoppingRecords, payload.id, 1)
      }
      localStorage.setItem('goods', JSON.stringify(state.shoppingRecords))
    }
  },
  actions: {
    async getTwotab (ctx, payload) {
      ctx.commit('showContent', false)
      const value = await api.getTowtab(payload)
      ctx.commit('twobarList', value.data)
      ctx.commit('showContent', true)
      //   console.log(value)
    },
    async getClassifyContent (ctx, payload) {
      const { page, sortType } = payload
      const type = payload.type || ctx.state.type
      ctx.commit('getTwotab', type)
      const value = await api.getClassifyContentList({ type, page, size: ctx.state.size, sort: sortType })
      ctx.commit('setClassifyContent', value.data.list)
      // console.log(ctx.state.classifyContent)
      if (value.data.total > ctx.state.classifyContent.length) {
        return true
      }
      return false
    }
  }
})
