<template>
  <div class="commodity-container">
    <div class="img">
      <img :src="Data.images[0]" alt="">
    </div>
    <div class="content">
      <div class="title overflow-hidden">{{Data.title}}</div>
      <div class="desc overflow-hidden">{{Data.desc}}</div>
      <div class="tag">
        <div v-for="it in Data.tags" :key="it">{{it}}</div>
      </div>
      <div class="price">￥{{Data.price}}</div>
      <div class="addsubtract">
        <div class="subtract" @touchend="addsubtract(Data.id, -1)" v-if="num">
          <img src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/rec.png">
        </div>
        <div class="num" v-if="num">{{num}}</div>
        <div class="add" @touchend="addsubtract(Data.id, 1)">
          <img src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/add.png">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  props: ['Data', 'num'],
  methods: {
    ...mapMutations(['localRecords']),
    addsubtract (id, number) {
      this.localRecords({ id, value: number })
    }
  }
}
</script>

<style lang="less" scoped>
.commodity-container{
  width: 100%;
  display: flex;
  height: 100px;
  .img{
    width: 90px;
    margin-right: 20px;
    img{
      width: 90px;
      height: 90px;
    }
  }
  .content{
    flex: 1;
    position: relative;
    > div {
      width: 170px;
    }
    .title{
      font-size: 13px;
      color: #1a1a1a;
      margin-top: 5px;
    }
    .desc{
      font-size: 11px;
      color: #aaa;
      margin-top: 5px;
    }
    .tag{
      display: flex;
      margin-top: 4px;
      > div{
        font-size: 10px;
        color: #aaa;
        padding: 2px;
        border: 1px solid #aaa;
        border-radius: 3px;
        margin-right: 5px;
      }
    }
    .price{
      font-size: 14px;
      color: #ff1a90;
      font-weight: 600;
      margin-top: 4px;
    }
    .addsubtract{
      display: flex;
      position: absolute;
      bottom: 12px;
