<template>
  <div class="classifycontent-conatiner">
      <div class="header">
        <div :class="{active: type == 'all'}" @click="changeType('all')">综合</div>
        <div :class="{active: type == 'sale'}" @click="changeType('sale')">销量</div>
        <div class="price" :class="{'price-up': type == 'price-up', 'price-down': type == 'price-down'}" @click="changeType('price')">价格</div>
      </div>
      <div class="bodyer">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" head-height="100">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有了"
            @load="onLoad"
          >
            <Commodity v-for="item in classifyContent" :key="item.id" :Data="item" :num="shoppingRecords[item.id]" />
          </van-list>
        </van-pull-refresh>
      </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import Commodity from '@/components/Commodity.vue'
export default {
  components: {
    Commodity
  },
  data () {
    return {
      type: 'all',
      isLoading: false, // 下拉刷新
      loading: false,
      finished: false,
      page: 1
    }
  },
  computed: {
    ...mapState({
      classifyContent: (state) => state.classifyContent,
      shoppingRecords: (state) => state.shoppingRecords
    })
  },
  methods: {
    ...mapMutations(['resetClassifyContent']),
    ...mapActions(['getClassifyContent']),
    changeType (type) {
      if (type === 'all') {
        this.type = 'all'
      } else if (type === 'sale') {
        this.type = 'sale'
      } else if (this.type === 'price-up') {
        this.type = 'price-down'
      } else {
        this.type = 'price-up'
      }
      this.onRefresh()
    },
    // 下拉刷新
    onRefresh () {
      this.isLoading = true
      this.finished = false
      this.loading = false
      this.page = 1
      this.resetClassifyContent()
      this.getClassifyContent({ page: 1, sortType: this.type })
      this.isLoading = false
    },
    // 加载更多
    async onLoad () {
      this.page += 1
      this.loading = true
      const resp = await this.getClassifyContent({ page: this.page, sortType: this.type })
      if (resp) {
        this.loading = false
      } else {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.classifycontent-conatiner{
  position: fixed;
  width: calc(100% - 79px);
  left: 79px;
  top: 135px;
  bottom: 50px;
  .header{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0 8px;
    box-sizing: border-box;
    > div {
        width: 50px;
        height: 25px;
        line-height: 25px;
        text-align: center;
        color: #cecece;
        position: relative;
    }
    .active, .price-up, .price-down {
        color: #ff1a90;
        font-weight: bold;
    }
    .price::before {
        content: '';
        border: 4px solid transparent;
        border-bottom-color: #aaa;
        position: absolute;
        top: 4px;
        right: 0;
    }
    .price::after {
        content: '';
        border: 4px solid transparent;
        border-top-color: #aaa;
        position: absolute;
        bottom: 4px;
        right: 0;
    }
    .price-up::before {
        border-bottom-color: #ff1a90;
    }
    .price-down::after {
        border-top-color: #ff1a90;
    }
  }
  .bodyer{
    width: 296px;
    position: fixed;
    top: 160px;
    bottom: 50px;
    right: 0;
    .van-pull-refresh{
      width: 100%;
      height: 100%;
      overflow: auto;
    }
  }
  .van-pull-refresh::-webkit-scrollbar{
    width: 0px;
    background: none;
  }
}
</style>
