<template>
  <div class="shopping-container">
    <div class="header">
      <van-nav-bar
        title="购物车"
        right-text="删除"
        @click-right="onClickRight"
      />
    </div>
    <div class="bodyer">
      <van-checkbox-group v-model="result" v-if="shoppingList.length > 0">
        <div v-for="item in shoppingList" :key="item.id" class="content" @click="del(item, shoppingRecords[item.id])">
          <van-checkbox :name="item.id"></van-checkbox>
          <Commodity :Data="item" :num="shoppingRecords[item.id]" />
        </div>
      </van-checkbox-group>
      <van-empty description="没有商品，亲！！！" v-else />
    </div>
    <div class="footer">
      <van-submit-bar :price="allprice" button-text="结算" @submit="onSubmit">
        <van-checkbox v-model="checked" @click="cancel">全选</van-checkbox>
      </van-submit-bar>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'
import { mapState, mapMutations } from 'vuex'
import Commodity from '@/components/Commodity.vue'
import api from '@/api/shopping.js'
export default {
  components: {
    Commodity
  },
  data () {
    return {
      checked: false,
      shoppingList: [],
      result: []
    }
  },
  computed: {
    ...mapState({
      shoppingRecords: (state) => state.shoppingRecords
    }),
    allprice () {
      const arr = []
      this.result.forEach((value) => {
        this.shoppingList.filter(item => {
          if (item.id === value) {
            if (!this.shoppingRecords[value]) {
              this.shoppingRecords[value] = 0
            }
            arr.push(item.price * this.shoppingRecords[value])
          }
        })
      })
      const all = arr.reduce((prev, next) => {
        return prev + next
      }, 0)
      return all * 100
    }
  },
  methods: {
    ...mapMutations(['localRecords']),
    onClickRight () {
      if (this.result.length === 0) {
        Toast('没有要删除商品')
      }
      this.result.forEach(id => {
        this.localRecords({ id: id, value: -2 })
        this.shoppingList = this.shoppingList.filter((item) => !this.result.includes(item.id))
      })
    },
    del (items, num) {
      if (!num) {
        this.shoppingList = this.shoppingList.filter((item) => {
          return item !== items
        })
      }
    },
    onSubmit () {
      Toast('请付钱')
    },
    cancel () {
      if (!this.checked) {
        this.result = []
      } else {
        this.result = []
        Object.keys(JSON.parse(localStorage.getItem('goods'))).forEach(value => this.result.push(+value))
      }
    }
  },
  watch: {
    result () {
      if (this.result.length === this.shoppingList.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    }
  },
  async created () {
    const goodslist = JSON.parse(localStorage.getItem('goods'))
    const goodslistStr = Object.keys(goodslist).join()
    const value = await api.getshopping({ value: goodslistStr })
    this.shoppingList = value.data.list
    // console.log(this.shoppingList)
  }
}
</script>

<style scoped lang="less">
.shopping-container{
  .bodyer{
    height: 520px;
    overflow: auto;
    padding: 0 20px;
    .content{
      display: flex;
      .van-checkbox{
        margin: 0 5px;
        width: 25px;
      }
    }
  }
  .footer{
    .van-submit-bar{
      bottom: 50px !important;
    }
  }
  .bodyer::-webkit-scrollbar{
    width: 0px;
    background: none;
  }
}
</style>
