<template>
  <div class="twotab-container" ref="twotab">
      <div
        class="twotab-item"
        v-for="(item, i) in twobarList"
        :key="item"
        :class="{active: index == i}"
        @touchend="scrollTo(i, $event)"
        @touchstart="move = false"
        @touchmove="move = true"
      >
        {{i == 0 ? '全部' : item}}
      </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import animation from '@/until/animation.js'
export default {
  data () {
    return {
      index: 0,
      move: false
    }
  },
  computed: {
    ...mapState({
      twobarList: (state) => state.twobarList
    })
  },
  methods: {
    ...mapMutations(['resetClassifyContent']),
    ...mapActions(['getClassifyContent']),
    scrollTo (i, e) {
      if (this.move) {
        return
      }
      this.index = i
      const itemTop = e.target.getBoundingClientRect().top
      const twotabTop = this.$refs.twotab.getBoundingClientRect().top
      const itemHeight = e.target.offsetHeight
      const twotabHeight = this.$refs.twotab.offsetHeight
      animation(this.$refs.twotab.scrollTop, itemTop + itemHeight / 2 - twotabTop - twotabHeight / 2, this.$refs.twotab, 'scrollTop')
      this.resetClassifyContent()
      this.getClassifyContent({ type: this.twobarList[i], page: 1, sortType: 'all' })
    }
  }
}
</script>

<style lang="less" scoped>
.twotab-container{
  position: fixed;
  width: 79px;
  // height: calc( 100% - 33px - 104px - 100px );
  top: 135px;
  bottom: 50px;
  overflow: auto;
  background-color: #f8f8f8;
  .twotab-item{
    width: 67px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    border-left: 6px solid #f8f8f8;
    border-right: 6px solid #f8f8f8;
  }
  .active{
    font-weight: bold;
    color: #ff1a90;
    border-left-color: #ff1a90;
  }
}
.twotab-container::-webkit-scrollbar{
  width: 0px;
  background: none;
}
</style>
